body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: pink;
}

.login-container {
    width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color:rgb(184, 157, 161);
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 100px;
}

.login-container h2 {
    text-align: center;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
}

.form-group input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.form-group button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.form-group button:hover {
    background-color: #0056b3;
}