header {
    background-color:pink;
    color: #fff;
    text-align: center;
    padding: 10px;
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

section {
    width: 45%;
    margin: 10px;
    padding: 10px;
    /* border: 1px solid #ddd; */
}

form {
    display: flex;
    flex-direction: column;
}

label {
    font-weight: bold;
}

input, textarea {
    margin: 5px 0;
    padding: 5px;
    border: 1px solid #ccc;
}

button {
    background-color: #333;
    color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
}

button:hover {
    background-color: #555;
}

address {
    font-style: normal;
}

a {
    text-decoration: none;
    color: #0077b6;
}

a:hover {
    text-decoration: underline;
}